.compare-bikes {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }

  .product {
    font-weight: 700;
    font-size: 24px;

    &__text {
      font-weight: 400;
      font-size: 16px;
    }
  }
}
